import React, { useState } from 'react';
import '../App.css';
import RangeSlider from './RangeSlider';
import {Switch} from 'react-native'; 
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function FormSenha() {
  const [useletter, setUseLetter] = useState(true);
  const [usenumber, setUseNumber] = useState(true);
  const [usecaracter, setUseCaracter] = useState(true);

  const [isSubmitted, setIsSubmitted] = useState(false);

  const isHidden = useState(true);

  const [passwordShown, setPasswordShown] = useState(false);
  const togglePasswordVisibility = () => {
    setPasswordShown(!passwordShown);
  };

  const toggleSwitchLetter = () => setUseLetter(previousState => !previousState);
  const toggleSwitchNumber = () => setUseNumber(previousState => !previousState);
  const toggleSwitcCaracter = () => setUseCaracter(previousState => !previousState);

  const [useSenha, setResponseSenha] = useState(null);
  const [useUrl, setResponseUrl] = useState(null);

  const handleSubmit = async (event) => {

    event.preventDefault();

    const form = event.target;
    const formData = new FormData(form);

    const tamanho = parseInt(formData.get('tamanho'));
    const letter = useletter ? 1 : 0;
    const nums = usenumber ? 1 : 0;
    const special = usecaracter ? 1 : 0;
    const views = parseInt(formData.get('views'));
    const valida = parseInt(formData.get('valida'));

    if (letter !== 1 && nums !== 1 && special !== 1) {
        toast.error('Escolha ao menos uma das opções de formato para a senha.', {
            position: toast.POSITION.TOP_RIGHT,
            autoClose: 2000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: false,
            progress: false,
            closeButton: false
          });
          return;
    }
    if (!isNaN(views) && (views < 1 || views > 100)) {
        toast.error('Em "Visualizações Máximas" escolha um valor entre 1 e 100', {
            position: toast.POSITION.TOP_RIGHT,
            autoClose: 3000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: false,
            progress: false,
            closeButton: false
          });
          return;
    }
    if (!isNaN(valida) && (valida < 1 || valida > 90)) {
        toast.error('Em "Validade (em Dias)" escolha um valor entre 1 e 90', {
            position: toast.POSITION.TOP_RIGHT,
            autoClose: 3000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: false,
            progress: false,
            closeButton: false
          });
          return;
    }
    
    try {
        document.getElementById('botao_f').disabled = true;

        const response = await fetch('https://endereco-api', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                tamanho,
                letter,
                nums,
                special,
                views,
                valida
            }),
            });
            const retorno = await response.json();

            if (retorno.body.senha) {
                setResponseSenha(retorno.body.senha);
                setResponseUrl("https://url-fron-visualiza-senha/"+retorno.body.url);
                setIsSubmitted(true);
            } else {
                toast.error(retorno.body.mensagem, {
                    position: toast.POSITION.TOP_RIGHT,
                    autoClose: 2000,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: false,
                    progress: false,
                    closeButton: false
                });
            }
        } catch (error) {
            toast.error('Opss... Ocorreu um erro ao executar isso', {
                position: toast.POSITION.TOP_RIGHT,
                autoClose: 2000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: false,
                progress: false,
                closeButton: false
            });
            
           document.getElementById('botao_f').disabled = false;
    }  
  };

  return (
    <div className="App">
        <div className="container">
        {!isSubmitted ? (
            <form onSubmit={handleSubmit}>
                
                <h1>Gerador de Senha</h1>
                <RangeSlider
                    min={8}
                    max={64}
                    step={1}
                    value={32}
                />
                <div className="options">
                    <Switch
                    id="letter"
                    name="letter"
                    value={useletter}
                    onValueChange={toggleSwitchLetter}
                    checked={useletter}
                    />
                    <span className="labelsCheck">Letras</span>
                    <Switch
                    id="nums"
                    name="nums"
                    onValueChange={toggleSwitchNumber}
                    value={usenumber}
                    />
                    <span className="labelsCheck">Números</span>
                    <Switch
                    id="special"
                    name="special"
                    onValueChange={toggleSwitcCaracter}
                    value={usecaracter}
                    />
                    <span className="labelsCheck">Caracteres Especiais</span>
                </div>
                <div>
                     <span className="labelinputgera">Visualizações Máximas:</span>
                    <input className="inputgera" type="number" min={1} max={100} name="views" defaultValue={1} />
                    <span className="separa">|</span>
                    <span className="labelinputgera">Validade (em Dias):</span>
                    <input className="inputgera" type="number" min={1} max={90} name="valida" defaultValue={1} />
                    
                </div>
                <div>
                    <button type="submit" id="botao_f">Gerar Senha</button>
                </div>
                <ToastContainer />
            </form>
        ):(
                <div>
                <h1>Senha Gerada com Sucesso</h1>
                <p>Aqui está o link para a visualização da senha:</p>
                <p className="linksenha">{useUrl}</p>
                <div className="sgerada">
                    <textarea
                        className="in_senha"
                        value={passwordShown ? useSenha : ""}
                        onChange={(e) => console.log(e.target.value)}
                        readOnly
                    ></textarea>
                    <button className="bt_senha" onClick={togglePasswordVisibility}>
                        {isHidden ? 'Mostrar Senha' : 'Ocultar'}
                    </button>
                </div>
                </div>
        )}
        </div>
    </div>
  );
}

export default FormSenha;
