import React from 'react';

class RangeSlider extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: props.value || 0
    };
  }

  handleChange = event => {
    const newValue = parseInt(event.target.value, 10);
    this.setState({ value: newValue });
  };

  render() {
    return (
      <div id="tamanho">
        <label htmlFor={this.props.id}>Tamanho da Senha {this.state.value}<br/></label>
        <input
          id={this.props.id}
          className="slider"
          name="tamanho"
          type="range"
          min={this.props.min}
          max={this.props.max}
          step={this.props.step}
          value={this.state.value}
          onChange={this.handleChange}
        />
      </div>
    );
  }
}

export default RangeSlider;